@import '../../global.scss';
header {
	background: $bg;
	color: #010405;
	a {
		color: #8babf1;
		text-align: center;
		text-decoration: none;
		padding: $large;
		display: block;
		&:hover {
			background: lighten(
				lightgray,
				5%
			);
		}
	}
	nav {
		display: flex;
		& > * {
			margin: auto;
			list-style-type: none;
		}
	}
	details {
		ul {
			background: $bg;
			position: absolute;
			& :hover {
				background: lighten(
					$bg,
					5%
				);
			}
		}
	}
	.logo {
		margin: auto;
		height: 4rem;
		width: 4rem;
		background: $dark;
		position: relative;
		& img {
			border-radius: $medium;
			height: 100%;
			width: auto;
		}
	}
}
@media (max-width: $mobile) {
	header {
		a {
			border-bottom: solid;
			padding: 0 !important;
		}
		menu li {
			margin: 0 0.25rem;
		}
	}
}
